import * as React from 'react';

interface Props {
    messages: string[];
}

const MessagesList = ({ messages }: Props) => {
    return (
        <div className="messages-list">
            {
                messages.map(message => (
                    <div className="message">
                        {message}
                    </div>
                ))
            }
        </div>
    )
}

export default MessagesList;
