import * as React from 'react';
import '../styles/login.scss';

interface Props {
    onLogin: () => any;
}

const LoginForm = ({ onLogin }: Props) => {
    const [username, setUsername] = React.useState('');

    const [password, setPassword] = React.useState('');

    const loginHandler = (e: React.MouseEvent) => {
        e.preventDefault();

        fetch('/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            }, 
            body: JSON.stringify({
                username,
                password  
            })
        })
            .then(res => {
                if (res.ok) {
                    onLogin();
                } else {
                    alert('Login failed');
                    throw new Error(`Login failed with status ${res.status} - ${res.text}`);
                }
            })
            .catch(err => console.error(err))
    }

    const registerHandler = (e: React.MouseEvent) => {
        e.preventDefault();

        fetch('/register', {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                username, 
                password
            })
        })
            .then(res => {
                if (res.ok) {
                    alert('user registered');
                } else {
                    alert('registration failed');
                    throw new Error(`Register failed with status ${res.status} - ${res.text}`);
                }
            })
            .catch(err => console.error(err))
    }

    return (
        <div className="login-wrapper">
            <form className="login-form">
                <label>Login</label>
                <input 
                    className="login-form__field" 
                    value={username} 
                    onChange={e => setUsername(e.target.value)} />
                <label>Password</label>
                <input 
                    className="login-form__field" 
                    value={password} 
                    type="password" 
                    onChange={e => setPassword(e.target.value)} />
                <button
                    className="login-form__submit-button" 
                    onClick={loginHandler} >Login</button>
                <button
                    className="login-form__submit-button" 
                    onClick={registerHandler}>
                    Register
                </button>

            </form>
        </div>
    )

}

export default LoginForm;
