import * as React from 'react';
import * as Cookies from 'js-cookie';
import MessagesList from './MessagesList';

interface Props {

}

interface State {
    messages: string[];
    inputValue: string;
}

class MainLayout extends React.Component<Props, State> {

    state = {
        messages: [],
        inputValue: ''
    }

    private webSocket: WebSocket;

    constructor(props) {
        super(props);

        const ws = new WebSocket('ws://' + window.location.host);

        ws.onopen = () => {
            console.log('ws open')
        }

        ws.onmessage = this.messageHandler;
        this.webSocket = ws;
    }

    private messageHandler = (e: MessageEvent) => {
        const data = JSON.parse(e.data),
            { type, content } = data;

        switch(type) {
            case ('message'): {
                this.setState({
                    messages: [...this.state.messages, content]
                })
            }
        }
    }

    render() {
        

        return (
            <>
                <MessagesList messages={this.state.messages}/>
                <input type="text" value={this.state.inputValue} onChange={this.inputHandler}/>
                <button onClick={this.buttonHandler}>Send message</button>
            </>
        )
    }

    private inputHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({
            inputValue: e.target.value
        })
    }

    private buttonHandler = () => {
        this.webSocket.send(JSON.stringify({
            type: 'message',
            content: this.state.inputValue
        }));
    }
}

export default MainLayout;
