import * as React from 'react';
import '../styles/styles.scss';
import LoginForm from './LoginForm';
import MainLayout from './MainLayout';

const App = () => {
    const [isLogined, logIn] = React.useState(false);

    return (
        <div className='root'>
            {
                isLogined ? 
                    <MainLayout/> :
                    <LoginForm onLogin={() => logIn(true)}/>
            }
        </div>
    )
} 

export default App;
